/* DropdownsComponent.css */

.dropdowns-container {
  display: flex;
  justify-content: center; /* Center the dropdowns horizontally */
  align-items: center; /* Center the dropdowns vertically */
  width: 100%; /* Full width of the viewport */
  background-color: #ff69b4; /* Example background color, match this to your design */
  padding: 10px 0; /* Increase padding to make the box taller */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: adds shadow for depth */
  z-index: 10; /* Ensures the dropdowns are above other content */
  overflow-x: visible; /* Allows horizontal scrolling if dropdowns overflow the width */
  position: fixed; /* Fixed position regardless of scroll */
  top: 70px; /* Height of the NavBar, adjust as needed */
  left: 0;
  right: 0;
  z-index: 999;
}

.dropdown {
  margin: 0 15px; /* Adjust spacing between dropdowns */
}

.dropdown-button {
  background-color: #ff69b4;
  border: 1px solid #ff69b4;
  padding: 3px 20px;
  cursor: pointer;
  border-radius: 5px;
  color: white; /* If you want the text to be white */
  border: none; /* If you don't want any border */
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dropdown-icon {
  margin-left: 8px; /* Adjust as needed */
  /* Add additional styling if needed */
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #ffffff;
  min-width: 160px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  z-index: 1000;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-item {
  display: block;
  padding: 12px 16px;
  text-decoration: none;
  color: black;
  border-bottom: 1px solid #f8f8f8;
}

.dropdown-item:last-child {
  border-bottom: none;
}

.dropdown-item:hover {
  background-color: #f2f2f2;
}

.login-container {
  margin-left: auto; /* This pushes the login button to the right */
}

.modal {
  display: none;
  /* Add other styles for the modal */
}

.modal.show {
  display: block;
}

/* login-modal.css */

.login-modal {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 1000;
}

.login-modal.show {
  display: block;
}

.modal-content {
  text-align: center;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 20px;
  color: #555;
}

label {
  display: block;
  margin-bottom: 10px;
}

input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

button {
  background: #ff69b4;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background: #ff69b4;
}
